<template>
  <div
    id="bar-container"
    :class="{
      old: $store.state.global.oldBrowser,
      minimized: scrollTop > 0 && !scrolledToTop,
    }"
  >
    <div class="mainControls">
      <ShuffleButton class="shuffle" />
      <ControlButton class="control-prev" :prev="true" />
      <ToggleButton class="toggleButton" :size="68" />
      <ControlButton class="control-next" />
      <RepeatButton class="repeatButton" />
    </div>
    <Visualizer3 class="visualizer" />
    <div class="duration">
      <span>{{ toHHMMSS(position / 1000) }}</span>
      /
      <span>{{
        currentSong ? toHHMMSS(currentSong.duration / 1000) : "00:00"
      }}</span>
    </div>
    <Volume class="volume" />
    <div class="plays-count">
      <PlayMiniIcon :style="{ transform: `scale(0.7)` }" />
      <p>{{ listensCount }}</p>
    </div>
  </div>
</template>

<script>
import ShuffleButton from "./ShuffleButton";
import RepeatButton from "./RepeatButton";
import ToggleButton from "./ToggleButton";
import ControlButton from "./ControlButton";
import Visualizer3 from "./Visualizer3";
import Volume from "./Volume";
import PlayMiniIcon from "@/components/Icons/PlayMini.vue";

export default {
  computed: {
    currentSong() {
      return this.$store.state.soundcloud.widgetState.currentSong;
    },
    widget() {
      return this.$store.state.soundcloud.widget;
    },
    position() {
      return this.$store.state.soundcloud.widgetState.position;
    },
    scrollTop() {
      return this.$store.state.global.scrollTop;
    },
    scrolledToTop() {
      return this.oldScrollPos - this.scrollTop >= 0;
    },
    listensCount() {
      if (this.currentSong)
        return this.currentSong.playback_count
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      else return 0;
    },
  },
  watch: {
    /* eslint-disable */
    scrollTop(newState, oldState) {
      this.oldScrollPos = oldState;
    },
    /* eslint-enable */
  },
  data: () => ({
    oldScrollPos: 0,
  }),
  components: {
    RepeatButton,
    ToggleButton,
    Visualizer3,
    ControlButton,
    ShuffleButton,
    Volume,
    PlayMiniIcon,
  },
  methods: {
    toHHMMSS(secs) {
      var sec_num = parseInt(secs, 10);
      var hours = Math.floor(sec_num / 3600);
      var minutes = Math.floor(sec_num / 60) % 60;
      var seconds = sec_num % 60;

      return [hours, minutes, seconds]
        .map((v) => (v < 10 ? "0" + v : v))
        .filter((v, i) => v !== "00" || i > 0)
        .join(":");
    },
  },
};
</script>

<style lang="scss">
#bar-container {
  position: fixed;
  bottom: 0;
  left: var(--sidebar-width);
  right: 0;
  -webkit-backdrop-filter: blur(64px);
  backdrop-filter: blur(64px);
  height: 100px;
  background-color: rgba(255, 255, 255, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: visible;
  padding: 0 16px;
  user-select: none;
  z-index: 99;

  .mainControls {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  .toggleButton {
    margin: 0 16px;
  }

  .repeatButton {
    margin-left: 20px;
  }

  .plays-count {
    display: flex;
    align-items: center;
    width: 130px;
    margin-left: 8px;

    p {
      margin-left: 6px;
      color: #fff;
      letter-spacing: 0.2em;
      font-size: 14px;
    }
  }

  .duration {
    display: flex;
    margin-left: 6px;
    color: #fff;
    letter-spacing: 0.2em;
    font-size: 12px;

    span {
      min-width: 56px;
      text-align: center;
    }
  }
}

@media screen and (max-width: 700px) {
  #bar-container {
    bottom: 76px;
  }
}

@media screen and (max-width: 500px) {
  #bar-container {
    bottom: 58px;
  }
}

@media screen and (max-width: 800px) {
  #bar-container {
    height: 168px;
    flex-wrap: wrap;
    justify-content: space-around;
    box-sizing: border-box;
    padding-bottom: 8px;

    &.minimized {
      height: 94px;

      .mainControls {
        display: none;
      }
    }

    .visualizer {
      margin-top: 32px;
      flex-basis: 100%;
      order: 2;

      p {
        text-align: center;
      }
    }

    .mainControls {
      flex-basis: 100%;
      order: 1;
      margin-top: 12px;
    }

    .duration {
      display: flex;
      justify-content: flex-end;
      margin-left: 0;
      order: 5;
      flex-basis: 50%;
    }

    .shuffle {
      display: flex;
      margin: 0;
    }

    .repeatButton {
      order: 5;
      margin: 0;
    }

    .control-prev {
      order: 2;
    }

    .toggleButton {
      margin: 0;
      order: 3;
      .button {
        width: 53px !important;
        height: 53px !important;
      }
    }

    .control-next {
      order: 4;
    }

    .volume {
      display: none;
    }

    .plays-count {
      justify-content: flex-start;
      margin: 0;
      height: 24px;
      order: 4;
      flex-basis: 50%;

      svg {
        transform: scale(0.5) !important;
      }
    }
  }
}

.old {
  &#bar-container {
    background-color: rgba(0, 0, 0, 0.8) !important;
  }
}

@supports (not (backdrop-filter: blur(48px))) and
  (not (-webkit-backdrop-filter: blur(48px))) {
  #bar-container {
    background-color: rgba(0, 0, 0, 0.8);
  }
}
</style>
