<template>
  <div id="player-container">
    <Background
      :url="
        $store.state.soundcloud.widgetState.artwork
          ? $store.state.soundcloud.widgetState.artwork
          : $store.state.soundcloud.widgetState.unknownSong
      "
    />
    <div id="player" v-if="loaded && !wfss">
      <div class="player-controller">
        <div
          class="sort"
          :class="{ hidden: $store.state.soundcloud.widgetState.shuffle }"
        >
          <div
            class="sort-item"
            :class="{ active: sortMode == 0 }"
            @click="setSortMode(0)"
          >
            Fresh
          </div>
          <div
            class="sort-item"
            :class="{ active: sortMode == 1 }"
            @click="setSortMode(1)"
          >
            Popular
          </div>
        </div>
        <Artworks class="artworks" />
        <h2 :class="{ loading: !loaded }">
          {{ loaded ? songTitle : "Загрузка..." }}
        </h2>
        <ul class="hashtags">
          <li class="tag" v-for="tag in tags" v-bind:key="tag._id">
            <span class="hashtag">#</span>{{ tag }}
          </li>
        </ul>
      </div>
      <div class="song-footer">
        <div
          class="song-text"
          v-html="
            songDescription ? convert(songDescription) : 'Описание недоступно'
          "
        ></div>
      </div>
      <!-- <SocialButtons /> -->
      <Social />
    </div>
    <div v-else>
      <Hexagon size="80px" />
    </div>
  </div>
</template>

<script>
import Playlists from "@/Playlists";
import Hexagon from "@/components/Elements/Hexagon";
import Background from "@/components/Player/Background";
import Artworks from "@/components/Player/Artworks";
import Social from "@/components/Home/Social";

export default {
  data: () => ({
    playlists: Playlists(),
    selectedPlaylist: 0,
    secretWordProgress: "",
    wfss: false,
    requiredSongPerma: null,
  }),
  components: {
    Artworks,
    Hexagon,
    Social,
    Background,
  },
  methods: {
    /* eslint-disable */
    convert: function (text) {
      var exp = /(\b(https?|ftp|file):\/\/[-A-Z0-9+&@#\/%?=~_|!:,.;]*[-A-Z0-9+&@#\/%=~_|])/gi;
      var text1 = text.replace(exp, "<a target='_blank' href='$1'>$1</a>");
      return text1;
    },
    /* eslint-disable */
    setSortMode(mode) {
      this.$store.commit("setWidgetState", {
        sortMode: mode,
      });
    },
    getTrackByPerma: function (perma) {
      return this.$store.state.soundcloud.songs.find(
        (track) => track.permalink == perma
      );
    },
    getTrackByTitle: function (title) {
      return this.$store.state.soundcloud.songs.find(
        (track) => track.title == title
      );
    },
    checkSecretPhrase(e) {
      this.secretWordProgress += e.code;
      this.secretWordProgress = this.secretWordProgress.replace(
        /Key|Numpad|Digit|Space/gi,
        ""
      );

      if (e.code == "Enter") this.secretWordProgress = "";
      else if (this.secretWordProgress == "INFINITEC0RE") {
        this.infPlayerMode_changePlaylist();
      }
    },
    infPlayerMode_changePlaylist() {
      let answer = prompt("Введите URL");

      if (answer) {
        this.$parent.changePlaylistURL(answer);

        this.playlists = [{ name: "InfPlayer" }];
        this.selectedPlaylist = 0;

        this.$store.commit("setWidgetState", {
          playlistIndex: 0,
          infplayer: true,
        });
      }

      this.secretWordProgress = "";
    },
    getSongIndex(songs, song) {
      return songs.findIndex((x) => x.id == song.id);
    },
    setTrack(perma) {
      let track = this.getTrackByPerma(perma);

      if (track && this.loaded) {
        this.$store.state.soundcloud.widget.skip(track.realIndex);
        this.$store.state.soundcloud.widget.seekTo(0);
        this.wfss = false;

        this.$store.commit("setWidgetState", {
          soundID: this.$store.state.soundcloud.songs.findIndex(
            (a) => a.id == track.id
          ),
        });
      } else if (!track && this.loaded) {
        this.wfss = false;
      }
    },
  },
  watch: {
    /* eslint-disable */
    currentSong(newSong, oldSong) {},
    songs(newSongs, oldSongs) {
      if (this.wfss) {
        this.setTrack(this.requiredSongPerma);
      }
    },
    initialized(newState, oldState) {
      if (this.$route.name == "PlayerSong") {
        let currentPlaylist = this.$store.state.soundcloud.widgetState
          .playlistName;
        let requiredPlaylist = this.$router.currentRoute.params.playlistName;

        if (requiredPlaylist) {
          this.wfss = true;

          requiredPlaylist = requiredPlaylist.replace(/_/g, " ");
          this.requiredSongPerma = this.$router.currentRoute.params.songID.slice(
            0
          );

          if (requiredPlaylist != this.playlists[0].name) {
            let playlist = this.playlists.find(
              (x) => x.name == requiredPlaylist
            );

            if (playlist) {
              let playlistIndex = this.playlists.findIndex(
                (x) => x.name == playlist.name
              );

              this.$store.commit("setWidgetState", {
                loaded: false,
              });

              this.$store.state.soundcloud.widget.load(playlist.url, {
                callback: () => {
                  this.$store.commit("setWidgetState", {
                    songs: [],
                    playlistIndex,
                    playlistName: playlist.name,
                  });

                  this.selectedPlaylist = playlistIndex;

                  this.$parent.refreshSongs(
                    this.$store.state.soundcloud.widget
                  );
                },
              });
            } else {
              this.wfss = false;
            }
          } else {
            this.setTrack(this.requiredSongPerma);
          }
        }
      } else {
        this.$store.state.soundcloud.widget.skip(this.songs[0].realIndex);
      }
    },
    /* eslint-enable */
  },
  computed: {
    currentSong() {
      return this.$store.state.soundcloud.widgetState.currentSong;
    },
    playlistsNames() {
      return this.playlists;
    },
    songDescription() {
      if (this.currentSong && this.currentSong.description)
        return this.currentSong.description.replace(
          /(?:\r\n|\r|\n)/g,
          "<br />"
        );
      else return "";
    },
    infPlayerMode() {
      return this.$store.state.soundcloud.infplayer;
    },
    sortMode() {
      return this.$store.state.soundcloud.sortMode;
    },
    loaded() {
      return this.$store.state.soundcloud.loaded;
    },
    songs() {
      return this.$store.state.soundcloud.songs;
    },
    initialized() {
      return this.$store.state.soundcloud.initialized;
    },
    songTitle() {
      if (this.currentSong) return this.currentSong.title;
      else return this.$store.state.soundcloud.songs[0].title;
    },
    tags() {
      if (!this.currentSong) return ["нет тега"];
      let tags = this.currentSong.tag_list;

      if (!tags) return ["нет тега"];

      tags = tags.match(/"[^"]+"|[\w]+/g);

      tags = tags.map((tag) => {
        return tag.replace(/"/g, "");
      });

      return tags;
    },
    songID() {
      return this.$route.params.songID;
    },
  },
  mounted() {
    this.selectedPlaylist = this.$store.state.soundcloud.widgetState.playlistIndex;
    document.addEventListener("keydown", this.checkSecretPhrase);
  },
  beforeDestroy() {
    document.removeEventListener("keydown", this.checkSecretPhrase);
  },
};
</script>

<style lang="scss" scoped>
#player-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  flex-direction: column;
  min-height: calc(100vh - 96px);
  overflow: hidden;
}

.sort {
  display: flex;
  justify-content: space-between;
  width: 160px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 1);
  margin-bottom: 32px;
  font-size: 16px;

  .sort-item {
    padding-bottom: 4px;
    color: rgba(255, 255, 255, 0.5);
    border: none;
    transition: 0.2s color;
    cursor: pointer;

    &.active {
      color: #fff;
      border-bottom: 2px solid #ff006e;
    }
  }
}

#player {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  position: relative;
  margin: auto;
  margin-top: 36px;
  padding-bottom: 24px;
  animation: appear 3s forwards;
  width: 100%;
  overflow: hidden;
}

h2 {
  text-align: center;
  margin-top: 3vh;
  color: #fff;
  font-weight: bold;
  font-size: 22px;
  max-width: 80%;
}

.player-controller {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 100vw;

  .controller-top {
    display: flex;
    position: relative;
    margin: 0 8px;
    margin-bottom: 24px;
    min-width: 400px;
  }
}

.hashtags {
  list-style: none;
  display: flex;
  justify-content: center;
  letter-spacing: 0.1em;
  flex-wrap: wrap;
  line-height: 24px;
  color: #fff;
  margin-bottom: 2vh;
  font-weight: bold;
  font-size: 10px;

  .tag {
    margin: 0 6px;
    position: relative;
    text-transform: uppercase;

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: -7px;
      width: 2px;
      height: 2px;
      background: rgba(255, 255, 255, 0.4);
      border-radius: 50%;
    }

    .hashtag {
      opacity: 0.4;
    }

    &:nth-last-child(1)::after {
      display: none;
    }
  }
}

.song-footer {
  text-align: center;
  color: #fff;
  font-size: 16px;
  font-weight: normal;
  max-width: 80%;

  .song-text {
    line-height: 25px;
    color: #fff;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.toggleButton {
  margin-right: 12px;
}

.duration {
  margin-left: 12px;
  color: rgb(255, 255, 255);
  font-size: 14px;
  text-align: center;
}

.loading {
  animation: loading 2s infinite;
}

.hidden {
  opacity: 0;
  pointer-events: none;
}

@media screen and (max-width: 800px) {
  #player {
    padding-bottom: 140px;
  }

  .repeatButton {
    order: 3;
    margin: 0 !important;
    width: 32px;
    height: 32px;
    margin-right: 8px !important;
  }

  .toggleButton {
    order: 4;
    margin: 0 !important;
  }

  .visualizer {
    order: 1;
    margin: 0 !important;
  }

  .duration {
    order: 2;
    flex-basis: 100% !important;
    margin: 0 !important;
    margin-bottom: 12px !important;
  }

  .volume {
    order: 5;
    margin: 0 !important;
    margin-bottom: 28px !important;
    margin-left: 8px !important;
  }
}

@media screen and (max-width: 650px) {
  #player-container {
    min-height: calc(100vh - 64px);

    .controller-top {
      min-width: 340px;
    }
  }
}

@media screen and (max-width: 600px) {
  h2 {
    font-size: 18px;
  }

  .song-footer {
    font-size: 14px;
  }

  #player {
    margin: auto !important;
    margin-top: 20px !important;
  }
}

@media screen and (max-width: 500px) {
  h2 {
    font-size: 16px;
  }

  .duration {
    margin-bottom: 6px !important;
  }
}

@media screen and (max-width: 359px) {
  #player-container {
    .controller-top {
      min-width: 300px;
    }
  }
}

@media screen and (max-width: 310px) {
  #player-container {
    .controller-top {
      min-width: 270px;
    }
  }
}

@keyframes appear {
  0% {
    transform: translateY(100%);
    opacity: 0;
  }

  25% {
    opacity: 0;
  }

  50% {
    transform: translateY(0);
    opacity: 1;
  }

  100% {
    visibility: visible;
  }
}

@keyframes overlay-appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes buttons-appear {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }

  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
</style>
