import Vue from "vue";
import Vuex from "vuex";
import SortSongs from "../scripts/SortSongs";
import ShuffleArray from "../scripts/ShuffleArray";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    global: {
      oldBrowser: false,
      scrollTop: 0
    },
    soundcloud: {
      songs: [],
      widget: null,
      loaded: false,
      initialized: false,
      unknownSong: require("@/assets/images/bmd_music.png"),
      sortMode: 0,
      infplayer: false,
      widgetState: {
        artwork: null,
        soundId: null,
        soundIndex: null,
        isPlaying: false,
        currentSong: null,
        repeat: false,
        playlistName: null,
        playlistIndex: 0,
        position: null,
        shuffle: 0,
      },
    },
  },
  mutations: {
    setWidgetState(state, obj) {
      let {
        songs,
        widget,
        artwork,
        soundId,
        soundIndex,
        isPlaying,
        currentSong,
        loaded,
        repeat,
        playlistName,
        playlistIndex,
        sortMode,
        needSort,
        position,
        initialized,
        shuffle,
        infplayer,
      } = obj;

      if (songs != undefined && songs.length > 0) {
        songs.forEach((song, index) => {
          if (song.realIndex == undefined) song.realIndex = index;
        });

        if (needSort) songs = SortSongs(songs, state.soundcloud.sortMode);

        if (state.soundcloud.widget && needSort) {
          state.soundcloud.widget.skip(songs[0].realIndex);
          state.soundcloud.widget.seekTo(0);
        }

        state.soundcloud.songs = songs;
      }

      if (widget != undefined) state.soundcloud.widget = widget;
      if (loaded != undefined) state.soundcloud.loaded = loaded;
      if (infplayer != undefined) state.soundcloud.infplayer = infplayer;
      if (initialized != undefined) state.soundcloud.initialized = initialized;
      if (sortMode != undefined) state.soundcloud.sortMode = sortMode;
      if (artwork != undefined) state.soundcloud.widgetState.artwork = artwork;
      if (soundId != undefined) state.soundcloud.widgetState.soundId = soundId;
      if (soundIndex != undefined)
        state.soundcloud.widgetState.soundIndex = soundIndex;
      if (isPlaying != undefined)
        state.soundcloud.widgetState.isPlaying = isPlaying;
      if (currentSong != undefined)
        state.soundcloud.widgetState.currentSong = currentSong;
      if (repeat != undefined) state.soundcloud.widgetState.repeat = repeat;
      if (position != undefined)
        state.soundcloud.widgetState.position = position;
      if (shuffle != undefined) state.soundcloud.widgetState.shuffle = shuffle;
      if (playlistName != undefined)
        state.soundcloud.widgetState.playlistName = playlistName;
      if (playlistIndex != undefined)
        state.soundcloud.widgetState.playlistIndex = playlistIndex;
    },
    shuffleTracks(state) {
      state.soundcloud.widgetState.shuffle ^= 1;

      if (state.soundcloud.widgetState.shuffle) {
        ShuffleArray(state.soundcloud.songs);
      } else {
        state.soundcloud.songs = SortSongs(
          state.soundcloud.songs,
          state.soundcloud.sortMode
        );
      }

      let currentSoundIndex = state.soundcloud.songs.findIndex(
        (track) => track.id == state.soundcloud.widgetState.currentSong.id
      );

      state.soundcloud.widgetState.soundIndex = currentSoundIndex;
    },
  },
  actions: {},
  modules: {},
});
