<template>
  <div class="top">
    <div class="arrow a-left">
      <button class="small" @click="goBack">
        <img src="../assets/images/icons/arrow.svg" alt="" />
      </button>
    </div>
    <div class="center">
      <div class="way" v-for="way in path" v-bind:key="way._id">
        <span>{{ way }}</span>
        <span class="divider">/</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    path: {
      type: Array,
      default: () => ["Домой", "Да"],
    },
  },
  methods: {
    goBack() {
      switch (this.path[this.path.length - 2]) {
        case "Домой":
          this.$router.push({ path: `/` });
          break;
        case "Артисты":
          this.$router.push({ path: `/artists` });
          break;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.top {
  position: relative;
  display: flex;
  box-sizing: border-box;
  padding: 12px;

  .center {
    font-size: 1.2rem;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: rgba(255, 255, 255, 0.5);

    .way {
      display: inline-block;

      &:nth-last-child(1) {
        color: #fff;
        font-weight: bold;

        .divider {
          display: none;
        }
      }

      .divider {
        margin: 0 6px;
      }
    }
  }

  .arrow {
    button {
      width: 50px;
      height: 50px;
    }
    img {
      width: 22px;
    }
  }
}

@media screen and (max-width: 600px) {
  .top {
    .center {
      font-size: 1rem;

      .way {
        .divider {
          margin: 0 3px;
        }
      }
    }

    .arrow {
      button {
        width: 40px;
        height: 40px;
      }
      img {
        width: 16px;
        margin-top: 3px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .top {
    .center {
      font-size: 0.8rem;
    }
  }
}
</style>