import SC from "./scripts/SC";
let initialized = false;

export default {
  initialize(ctx) {
    if (initialized) return;
    initialized = true;

    const {
      READY,
      ERROR,
      PLAY,
      FINISH,
      PLAY_PROGRESS,
      PAUSE,
    } = SC.Widget.Events;

    let widget = (ctx.widget = SC.Widget(ctx.$refs.sc));

    widget.load(ctx.playlists[0].url);

    ctx.$store.commit("setWidgetState", {
      playlistName: ctx.playlists[0].name,
      playlistIndex: 0,
    });

    widget.bind(READY, () => {
      ctx.log("SoundCloud виджет инициализирован", "h1");
      ctx.log("Разработано InfiniteC0re", "developer");

      ctx.$store.commit("setWidgetState", {
        widget,
      });

      ctx.refreshSongs(widget);
      widget.setVolume(50);
    });

    widget.bind(ERROR, (e) => {
      ctx.log(e, "error");
    });

    widget.bind(PAUSE, () => {
      ctx.$store.commit("setWidgetState", {
        isPlaying: false,
      });
    });

    widget.bind(PLAY, (e) => {
      const { soundId } = e;

      ctx.$store.commit("setWidgetState", {
        isPlaying: true,
      });

      if (ctx.$store.state.soundcloud.widgetState.soundId != soundId) {
        let track = ctx.getTrackByID(soundId);

        let soundIndex = ctx.getTrackIndex(track);
        let currentSong = track;
        let artwork = track.artwork_url
          ? track.artwork_url.replace("large.jpg", "") + "t500x500.jpg"
          : ctx.$store.state.soundcloud.unknownSong;

        ctx.$store.commit("setWidgetState", {
          soundIndex,
          soundId,
          currentSong,
          artwork,
          isPlaying: true,
        });

        document.querySelectorAll("link").forEach((elem) => {
          elem.disabled = false;
        });
      }
    });

    widget.bind(PLAY_PROGRESS, (e) => {
      if (e.currentPosition >= ctx.currentSong.duration - 160) {
        let track = ctx.getTrackByID(e.soundId);

        if (ctx.$store.state.soundcloud.widgetState.repeat) {
          widget.seekTo(0);
        } else {
          let lastSong = ctx.songs[ctx.songs.length - 1];

          if (track.id != lastSong.id) {
            let trackIndex = ctx.getTrackIndex(track) + 1;
            widget.skip(ctx.songs[trackIndex].realIndex);
            widget.seekTo(0);
          }
        }
      }
    });

    widget.bind(FINISH, (e) => {
      let lastSong = ctx.songs[ctx.songs.length - 1];

      if (
        e.soundId == lastSong.id &&
        !ctx.$store.state.soundcloud.widgetState.repeat
      ) {
        widget.skip(ctx.songs[0].realIndex);
        widget.seekTo(0);
      }
    });

    ctx.updater = setInterval(() => {
      widget.getPosition((position) => {
        ctx.$store.commit("setWidgetState", {
          position,
        });
      });
    }, 1000);
  },
};
