<template>
  <div class="button-container">
    <button class="button" @click="clickHandle" :style="elemSize">
      <play v-if="!isPlaying" />
      <pause v-else />
    </button>
    <p v-if="text >= 0">{{ text }}</p>
  </div>
</template>

<script>
import Pause from "@/components/Icons/Pause.vue";
import Play from "@/components/Icons/Play.vue";
export default {
  components: { Pause, Play },
  computed: {
    isPlaying() {
      if (
        this.strict &&
        this.$store.state.soundcloud.widgetState.currentSong &&
        this.$store.state.soundcloud.widgetState.currentSong.title != this.songTitle
      )
        return false;
      else return this.$store.state.soundcloud.widgetState.isPlaying;
    },
    elemSize() {
      return {
        width: `${this.size}px`,
        height: `${this.size}px`,
      };
    },
    iconScale() {
      return {
        transform: `scale(${this.size < 80 ? 0.8 : 1})`,
      };
    },
  },
  props: {
    text: {
      type: Number,
      default: -1,
    },
    strict: {
      type: Boolean,
      default: false,
    },
    songTitle: {
      type: String,
      default: "",
    },
    index: {
      type: Number,
      default: 0,
    },
    size: {
      type: Number,
      default: 80,
    },
  },
  methods: {
    clickHandle() {
      if (
        this.strict &&
        this.$store.state.soundcloud.widgetState.currentSong.title != this.songTitle
      ) {
        this.$store.state.soundcloud.widget.skip(this.index);
      } else {
        this.$store.state.soundcloud.widget.toggle();
        this.$store.state.soundcloud.widget.getPosition((position) => {
          this.$store.state.soundcloud.widget.isPaused((isPaused) => {
            this.$store.commit("setWidgetState", {
              isPlaying: !isPaused,
              position,
            });
          });
        });
      }
    },
  },
  mounted() {
    if (this.$store.state.soundcloud.widget)
      this.$store.state.soundcloud.widget.isPaused((isPaused) => {
        this.$store.commit("setWidgetState", {
          isPlaying: !isPaused,
        });
      });
  },
};
</script>

<style lang="scss" scoped>
.button-container {
  display: flex;
  flex-direction: column;
  position: relative;
}

.button {
  outline: none;
  border: none;
  margin-left: 24px;
  cursor: pointer;
  transition: 0.1s;
  text-transform: uppercase;
  box-shadow: 0px 14px 24px rgba(82, 41, 205, 0.1);
  border-radius: 70px;
  font-family: "bmd-bold";
  color: #4514a3;
  background: transparent;
  font-size: 16px;
  height: 65px;
  width: 108px;

  svg {
    width: 100%;
    height: 100%;
    * {
      transition: 0.2s fill;
    }
  }
}

.button:hover {
  transform: scale(1.12);
}

.button:active {
  transform: scale(0.92);
}

.button:disabled {
  cursor: auto;
  color: gray;
  background: #d3d3d3;
}

.button:disabled:hover {
  transform: scale(1);
}

.button {
  margin: 0;
  border-radius: 100px !important;
  width: 80px;
  height: 80px;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fa-play {
  margin-left: 4px;
}

p {
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  margin-top: 8px;
  text-align: center;
  position: absolute;
  bottom: -30px;
  left: 0;
  right: 0;
}

@media screen and (max-width: 500px) {
  .button {
    width: 60px;
    height: 60px;

    svg {
      transform: scale(0.8);
    }
  }

  p {
    font-size: 14px;
    bottom: -23px;
  }
}
</style>
