<template>
  <div id="volumeButton">
    <div class="volumepanel" :class="{ show: pressed }">
      <div class="volumepanel-bar-thumb" id="volumeThumbBar" ref="bar" @mousedown="mousedownHandler">
        <div class="volumepanel-bar" id="volumeBar" :style="barHeight"></div>
      </div>
    </div>
    <div class="icon" @click="icon_handleClick">
      <FullVolumeIcon v-if="volume == 100" />
      <MiddleVolumeIcon v-else-if="volume >= 50" />
      <LowVolumeIcon v-else-if="volume > 0" />
      <NoVolumeIcon v-else-if="volume == 0" />
    </div>
  </div>
</template>

<script>
import FullVolumeIcon from "@/components/Icons/FullVolume";
import MiddleVolumeIcon from "@/components/Icons/MiddleVolume";
import LowVolumeIcon from "@/components/Icons/LowVolume";
import NoVolumeIcon from "@/components/Icons/NoVolume";

export default {
  components: {
    FullVolumeIcon,
    MiddleVolumeIcon,
    LowVolumeIcon,
    NoVolumeIcon,
  },
  data: () => ({
    volume: 100,
    prevVolume: 100,
    pressed: false,
    lastMouse: {
      lastY: null,
      deltaY: null,
      moved: false,
    },
  }),
  computed: {
    barHeight() {
      return {
        height: `${this.volume}%`,
      };
    },
  },
  mounted() {
    if (this.$store.state.widget)
      this.$store.state.widget.getVolume((volume) => {
        this.volume = volume;
      });

    document.addEventListener("mousemove", this.mousemoveHandler);
    document.addEventListener("mouseup", this.mouseupHandler);
  },
  beforeDestroy() {
    document.removeEventListener("mousemove", this.mousemoveHandler);
    document.removeEventListener("mouseup", this.mouseupHandler);
  },
  methods: {
    mousedownHandler(e) {
      this.pressed = true;
      this.lastMouse.lastY = e.clientY;

      // установить новую громкость
      let height = this.$refs.bar.getBoundingClientRect().height;
      let newVolume = ((height - e.offsetY) * 100) / height;
      this.volume = this.clamp(newVolume, 0, 100);

      this.$store.state.soundcloud.widget.setVolume(this.volume);
    },
    clamp(val, min, max) {
      return Math.min(Math.max(val, min), max);
    },
    mousemoveHandler(e) {
      if (this.pressed && this.lastMouse.lastY >= 0) {
        let { clientY } = e;
        this.lastMouse.deltaY = clientY - this.lastMouse.lastY;

        this.lastMouse.lastY = clientY;

        let height = this.$refs.bar.getBoundingClientRect().height;
        let newVolume = this.volume - (this.lastMouse.deltaY * 100) / height;
        this.volume = this.clamp(newVolume, 0, 100);

        this.$store.state.soundcloud.widget.setVolume(this.volume);
      }
    },
    mouseupHandler() {
      this.pressed = false;
    },
    icon_handleClick() {
      let { volume } = this;

      if (document.body.offsetWidth < 1000) return;

      if (this.volume > 0) {
        this.prevVolume = volume;
        this.volume = 0;

        this.$store.state.soundcloud.widget.setVolume(this.volume);
      } else if (this.volume == 0) {
        this.volume = this.prevVolume;

        this.$store.state.soundcloud.widget.setVolume(this.volume);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
#volumeButton {
  margin-left: 12px;
  position: relative;
  padding-top: 27px;
  margin-bottom: 27px;

  .icon {
    width: 32px;
    display: flex;
    justify-content: center;
    cursor: pointer;
  }

  &:hover .volumepanel {
    display: flex;
    opacity: 1;
  }

  .volumepanel {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 16px;
    width: 32px;
    height: 126px;
    background: rgb(0, 0, 0, 0.6);
    backdrop-filter: blur(48px);
    clip-path: polygon(100% 80%, 75% 80%, 50% 85%, 25% 80%, 0 80%, 0 0, 100% 0);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 14px;
    padding-bottom: 40px;
    box-sizing: border-box;
    display: none;
    opacity: 0;
    cursor: default;

    &.show {
      display: flex;
      opacity: 1;
    }

    .volumepanel-bar-thumb {
      width: 4px;
      height: 100%;
      background: rgba(255, 255, 255, 0.2);
      position: relative;
      cursor: pointer;

      .volumepanel-bar {
        background: rgba(255, 255, 255, 0.3);
        height: 50%;
        width: 100%;
        position: absolute;
        bottom: 0;
        pointer-events: none;

        &::after {
          content: "";
          position: absolute;
          top: -5px;
          left: 50%;
          transform: translateX(-50%);
          min-height: 10px;
          min-width: 10px;
          background: rgb(230, 230, 230);
          border-radius: 50%;
          transition: 0.2s transform;
          pointer-events: none;
        }
      }

      &:hover {
        .volumepanel-bar::after {
          transform: translateX(-50%) scale(1.2);
        }
      }
    }
  }
}
</style>
