<template>
  <div id="app" :class="{ songBarVisible }">
    <Sidebar />
    <div class="main-content" ref="content">
      <router-view />
    </div>
    <Bar v-if="songBarVisible" />
    <iframe
      ref="sc"
      hidden
      width="100%"
      height="450"
      scrolling="no"
      frameborder="no"
      allow="autoplay"
      src="https://w.soundcloud.com/player/?url=https://api.soundcloud.com/playlists/1024488982%3Fsecret_token%3Ds-t3rIoE0luqj&color=%23e81387&auto_play=false&hide_related=false&show_comments=true&show_user=true&show_reposts=false&show_teaser=true"
    ></iframe>
  </div>
</template>


<script>
import "@/assets/fonts/fonts.css";
import Sidebar from "./components/Sidebar.vue";
import Playlists from "./Playlists";
import Bar from "@/components/Player/Bar";

var consoleStyles = {
  h1: "font: 2.5em/1 Arial; color: crimson;",
  h2: "font: 2em/1 Arial; color: orangered;",
  h3: "font: 1.2em/1 Arial; color: olivedrab;",
  h4: "font: 1.2em/1 Arial; color: orangered;",
  developer: "font: 1.2em/1 Arial; color: orangered;",
  warn:
    "padding: 0.5rem; background: crimson; font: 1.6em/1 Arial; color: white;",
};

export default {
  components: {
    Sidebar,
    Bar,
  },
  data: () => ({
    widget: null,
    playlists: Playlists(),
    updater: null,
    socket: null,
  }),
  methods: {
    getTracks: function (callback) {
      this.widget.getSounds((songs) => {
        this.$store.commit("setWidgetState", {
          songs,
          needSort: false,
        });
        callback();
      });
    },
    log: function (msg, style) {
      if (!style || !consoleStyles[style]) {
        style = "bold";
      }
      console.log("%c" + msg, consoleStyles[style]);
    },
    getTrackByID: function (id) {
      return this.songs.find((track) => track.id == id);
    },
    getTrackIndex: function (song) {
      return this.songs.findIndex((x) => x.id == song.id);
    },
    getTrackIndexWithSongs: function (songs, song) {
      return songs.findIndex((x) => x.id == song.id);
    },
    refreshSongs(widget) {
      let loaderInterval = setInterval(() => {
        this.getTracks(() => {
          if (!this.songs.find((track) => track.title == undefined)) {
            clearInterval(loaderInterval);
            this.log("Список треков загружен: " + this.songs.length, "h4");

            this.$store.commit("setWidgetState", {
              widget,
              loaded: true,
              songs: this.songs,
              needSort: true,
              initialized: true,
              shuffle: 0,
              startPlayback: this.$route.name != "PlayerSong",
            });
          }
        });
      }, 500);
    },
    sendSongInfoToIO(socket) {
      if (this.currentSong && socket.connected)
        socket.emit("song_info", this.currentSong);
    },
    sendSongPosToIO(socket, position) {
      if (this.currentSong && socket.connected) {
        if (this.$store.state.soundcloud.widgetState.isPlaying) {
          socket.emit("song_pos", this.currentSong.duration - position);
        } else {
          socket.emit("song_pos", null);
        }
      }
    },
  },
  watch: {
    /* eslint-disable */
    sortMode: function (newMode, oldMode) {
      let songs = this.songs;

      this.$store.commit("setWidgetState", {
        songs,
        needSort: true,
        soundIndex: this.getTrackIndexWithSongs(
          songs,
          this.$store.state.soundcloud.widgetState.currentSong
        ),
      });
    },
    currentSong: function (newSong, oldSong) {
      document.querySelector("title").innerText = `${newSong.title} - BMD.GG`;
    },
    /* eslint-enable */
  },
  computed: {
    songs() {
      return this.$store.state.soundcloud.songs;
    },
    sortMode() {
      return this.$store.state.soundcloud.sortMode;
    },
    currentSong() {
      return this.$store.state.soundcloud.widgetState.currentSong;
    },
    songBarVisible() {
      return (
        this.$store.state.soundcloud.loaded &&
        (this.$store.state.soundcloud.widgetState.isPlaying ||
          this.$route.name == "Artist")
      );
    },
  },
  beforeMount() {
    this.$store.commit("setWidgetState", {
      loaded: false,
      infplayer: false,
    });
  },
  mounted() {
    const appHeight = () => {
      const doc = document.documentElement;
      doc.style.setProperty("--app-height", `${window.innerHeight}px`);
    };

    window.addEventListener("resize", appHeight);
    appHeight();

    this.$refs.content.addEventListener("scroll", (e) => {
      this.$store.state.global.scrollTop = e.target.scrollTop;
    });
  },
};
</script>

