<template>
  <div class="artists-root">
    <PathHeader :path="['Домой', 'Артисты']" />
    <div class="cards">
      <router-link
        v-for="(artist, i) in artists"
        v-bind:key="artist._id"
        class="card"
        :to="'/artist/' + artist.name.replace(/ /gi, '_')"
        :style="{animationDelay: `${i/5}s`}"
      >
        <ArtistCard
          :name="artist.name"
          :avatar="artist.avatar"
          :color="artist.color"
        />
      </router-link>
    </div>
  </div>
</template>

<script>
import PathHeader from "@/components/PathHeader";
import ArtistCard from "@/components/ArtistList/ArtistCard";
import Artists from "@/Artists";

export default {
  name: "Artist",
  components: {
    PathHeader,
    ArtistCard,
  },
  data: () => ({
    artists: Artists(),
  }),
};
</script>

<style lang="scss" scoped>
.artists-root {
  overflow: auto;

  .cards {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    place-items: center;

    a {
      width: 100%;
      display: flex;
      justify-content: center;
    }

    .card {
      transform: translateX(-120%);
      animation: appear 0.6s ease forwards;

      @keyframes appear {
        0% {
          transform: translateX(-120%);
        }

        100% {
          transform: translateX(0);
        }
      }
    }
  }
}
</style>