<template>
  <div id="background" :class="{ old: $store.state.global.oldBrowser }">
    <div
      class="img"
      :style="image"
      draggable="false"
      onmousedown="return false"
    />
  </div>
</template>

<script>
export default {
  props: {
    url: {
      type: String,
      default: "",
    },
  },
  computed: {
    image() {
      return {
        background: `url(${this.url})`,
      };
    },
  },
};
</script>

<style scoped>
#background {
  position: fixed;
  top: 0;
  left: var(--sidebar-width);
  right: 0;
  z-index: 0;
  bottom: 0;
  background: radial-gradient(ellipse at center top, #4e00b4, #1b005a);
}

#background::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  backdrop-filter: blur(48px);
  -webkit-backdrop-filter: blur(48px);
  background-color: rgba(0, 0, 0, 0.5);
}

.img {
  width: 100%;
  height: 100%;
  background-size: cover !important;
  background-position: center !important;
}

.old .img {
  background: radial-gradient(
    ellipse at center top,
    #4e00b4,
    #1b005a
  ) !important;
}
</style>