<script>
import PathHeader from "@/components/PathHeader";
import Artists from "@/Artists";

export default {
  components: {
    PathHeader,
  },
  data: () => ({
    artistList: [],
  }),
  methods: {
    goto(artist) {
      this.$router.push({ path: `/artist/${artist.name.replace(/ /g, "_")}` });
    },
    update() {
      let currentArtistID = Artists().findIndex(
        (t) => t.name == this.$parent.artist.name
      );

      for (let i = -2; i < 3; i++) {
        this.artistList[i + 2] = Artists()[currentArtistID + i]
          ? Artists()[currentArtistID + i]
          : null;
      }

      this.$forceUpdate();
    },
  },
  mounted() {
    this.update();
  },
};
</script>

