<template>
  <div class="artist-root" v-if="artist.name">
    <Header ref="header" />
    <!-- <div class="posts">
      <Post
        v-for="i in 9"
        v-bind:key="i"
        title="Intergalactic Black Mirror Designer [raw beat]"
        :loaded="posts[i - 1] ? true : false"
        :imageURL="posts[i] ? posts[i].thumbnail_src : ''"
        :type="
          posts[i] ? (posts[i].__typename == 'GraphVideo' ? 2 : 1) : -1
        "
        :likes="posts[i] ? posts[i].edge_liked_by.count : 0"
      />
    </div> -->
    <Player ref="player" />
  </div>
</template>

<script>
import Header from "@/components/Artist/Header";
import Player from "@/components/Player";
import Artists from "@/Artists";
import SoundCloud from "@/App.SoundCloud";

export default {
  name: "Artist",
  components: {
    Header,
    Player,
  },
  data: () => ({
    artist: {},
    posts: [],
  }),
  methods: {
    changePlaylistURL(url) {
      let widget = this.$store.state.soundcloud.widget;

      this.$store.commit("setWidgetState", {
        loaded: false,
      });

      widget.load(url, {
        callback: () => {
          this.$store.commit("setWidgetState", {
            songs: [],
          });
          this.$parent.refreshSongs(widget);
        },
      });
    },
    setArtist() {
      let name = this.$route.params.artistName.replace(/_/g, " ");
      this.artist = Artists().find((t) => t.name == name);
      this.changePlaylistURL(this.artist.soundcloud);
    },
  },
  watch: {
    /* eslint-disable */
    $route(oldValue, newValue) {
      this.setArtist();
      this.$refs.header.update();
    },
    initialized(oldValue, newValue) {
      this.setArtist();
    },
  },
  computed: {
    initialized() {
      return this.$store.state.soundcloud.initialized;
    },
  },
  mounted() {
    SoundCloud.initialize(this.$parent);
    if (this.initialized) this.setArtist();
  },
};
</script>

<style lang="scss" scoped>
.artist-root {
  overflow: auto;

  .posts {
    width: 100%;
    max-width: var(--max-content-width);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 32px;
    margin: auto;
    margin-top: 64px;
  }
}
</style>