<template>
  <div class="artworks">
    <div
      class="art"
      v-bind:key="index"
      v-for="index in 9"
      @click="artworkClick(-5 + index)"
      :class="{ invisible: isInvisible(-5 + index), topIndex: index < 5 }"
      :style="{
        backgroundImage: `url(${artworkURL(-5 + index)})`,
        zIndex: index < 5 ? 9 - index : 5,
      }"
    >
      <div class="tooltip" v-if="index != 5">{{ titleGetter(-5 + index) }}</div>
    </div>
    <!-- @mousedown="
        () => {
          if (-5 + index == 0) mousedownHandler();
        }
      " -->
  </div>
</template>

<script>
export default {
  data: () => ({
    pressed: false,
    lastMouse: {
      x: null,
      pressedTime: null,
      unpressedTime: null,
      r: 0,
    },
  }),
  methods: {
    artworkClick(i) {
      if (i != 0) this.skipTo(i);
    },
    // showInfo() {
    //   this.$router.push({
    //     name: "Player",
    //     params: {
    //       playlistName: this.$store.state.soundcloud.widgetState.playlistName.replace(
    //         / /g,
    //         "_"
    //       ),
    //       songID: this.$store.state.soundcloud.widgetState.currentSong.permalink,
    //     },
    //   });
    // },
    // mousemoveHandler(e) {
    //   if (this.pressed) {
    //     if (this.lastMouse.x != null) {
    //       let diff = e.clientX - this.lastMouse.x;
    //       this.lastMouse.r += diff;

    //       let store = this.$store;
    //       let widget = store.state.soundcloud.widget;

    //       let soundIndex = null;

    //       if (this.lastMouse.r > 200) {
    //         this.lastMouse.r = 0;
    //         soundIndex = store.state.soundcloud.widgetState.currentSong.realIndex + 1;
    //       } else if (this.lastMouse.r < -200) {
    //         this.lastMouse.r = 0;
    //         soundIndex = store.state.soundcloud.widgetState.currentSong.realIndex - 1;
    //       }

    //       if (soundIndex != null && store.state.soundcloud.songs[soundIndex]) {
    //         widget.skip(soundIndex);
    //         widget.seekTo(0);
    //       }
    //     }
    //     this.lastMouse.x = e.clientX;
    //   }
    // },
    // mouseupHandler() {
    //   if (this.pressed) {
    //     this.pressed = false;
    //     this.lastMouse.x = null;
    //     this.lastMouse.r = 0;
    //     this.lastMouse.unpressedTime = Date.now();
    //   }
    // },
    // mousedownHandler() {
    //   this.lastMouse.pressedTime = Date.now();
    //   this.pressed = true;
    // },
    artworkGetter(index) {
      const store = this.$store;
      let song = store.state.soundcloud.songs[index];
      if (song) {
        return song.artwork_url
          ? song.artwork_url.replace("large.jpg", "") + "t500x500.jpg"
          : store.state.soundcloud.unknownSong;
      } else {
        return store.state.soundcloud.unknownSong;
      }
    },
    titleGetter(index) {
      const store = this.$store;

      let currentSongIndex = store.state.soundcloud.widgetState.soundIndex;
      let soundIndex = currentSongIndex + index;

      let song = store.state.soundcloud.songs[soundIndex];
      if (song) {
        return song.title;
      } else {
        return "Unknown";
      }
    },
    artworkURL(index) {
      const store = this.$store;

      let currentSongIndex = store.state.soundcloud.widgetState.soundIndex;
      let soundIndex = currentSongIndex + index;

      if (index != 0) return this.artworkGetter(soundIndex);
      else return store.state.soundcloud.widgetState.artwork;
    },
    skipTo(index) {
      const store = this.$store;

      if (index == 0) return;

      let currentSongIndex = store.state.soundcloud.widgetState.soundIndex;
      let soundIndex = currentSongIndex + index;
      let song = store.state.soundcloud.songs[soundIndex];

      if (song) {
        store.state.soundcloud.widget.skip(song.realIndex);
        store.state.soundcloud.widget.seekTo(0);

        this.$store.commit("setWidgetState", {
          position: 0,
        });
      }
    },
    isInvisible(index) {
      const store = this.$store;

      let currentSongIndex = store.state.soundcloud.widgetState.soundIndex;
      let soundIndex = currentSongIndex + index;

      if (!store.state.soundcloud.songs[soundIndex]) return true;
    },
  },
  computed: {
    soundIndex() {
      return this.$store.state.soundcloud.widgetState.soundIndex;
    },
  },
  mounted() {
    document.addEventListener("mousemove", this.mousemoveHandler);
    document.addEventListener("mouseup", this.mouseupHandler);
  },
  beforeDestroy() {
    document.removeEventListener("mousemove", this.mousemoveHandler);
    document.removeEventListener("mouseup", this.mouseupHandler);
  },
  watch: {
    /* eslint-disable */
    soundIndex(oldVal, newVal) {
      this.$forceUpdate();
    },
  },
};
</script>

<style scoped>
.artworks {
  display: flex;
  align-items: center;
  justify-content: center;
}

.artworks .art {
  border-radius: 25px;
  margin: 0 8px;
  cursor: pointer;
  transition: 0.2s opacity, 0.2s transform;
  box-shadow: 0px 46px 40px -30px rgba(0, 0, 0, 0.5);
  background: rgba(0, 0, 0, 0.4);
  background-position: center;
  background-size: cover;
}

.topIndex {
  z-index: 99;
}

.artworks .art .tooltip {
  max-width: 200px;
}

.artworks .art:nth-child(1),
.artworks .art:nth-child(9) {
  width: 4vh;
  height: 4vh;
  opacity: 0.2;
  border-radius: 4px;
}

.artworks .art:nth-child(2),
.artworks .art:nth-child(8) {
  width: 5.2vh;
  height: 5.2vh;
  opacity: 0.3;
  border-radius: 6px;
}

.artworks .art:nth-child(3),
.artworks .art:nth-child(7) {
  width: 10vh;
  height: 10vh;
  opacity: 0.6;
  border-radius: 8px;
}

.artworks .art:nth-child(4),
.artworks .art:nth-child(6) {
  width: 18vh;
  height: 18vh;
  opacity: 0.9;
  border-radius: 12px;
}

.artworks .art:nth-child(5) {
  width: 40vh;
  height: 40vh;
  cursor: default;
  position: relative;
  z-index: 4 !important;
}

.artworks .art:hover {
  transform: scale(1.05);
  opacity: 1;
}

.artworks .art:nth-child(5):hover {
  transform: none;
}

img {
  width: 100%;
  height: 100%;
}

.invisible {
  cursor: default !important;
  opacity: 0 !important;
  visibility: hidden;
}

img {
  -webkit-user-drag: none;
  -ms-user-drag: none;
  -moz-user-drag: none;
}

@media screen and (max-width: 1000px) {
  .artworks div:nth-child(1),
  .artworks div:nth-child(9) {
    width: 4vh !important;
    height: 4vh !important;
  }
  .artworks div:nth-child(2),
  .artworks div:nth-child(8) {
    width: 5.2vh !important;
    height: 5.2vh !important;
  }
  .artworks div:nth-child(3),
  .artworks div:nth-child(7) {
    width: 6vh !important;
    height: 6vh !important;
  }
  .artworks div:nth-child(4),
  .artworks div:nth-child(6) {
    width: 8vh !important;
    height: 8vh !important;
  }
  .artworks div:nth-child(5) {
    width: 30vh !important;
    height: 30vh !important;
  }
}
</style>