<template>
  <div
    class="post"
    :class="{ loading: !loaded }"
    :style="{
      backgroundImage: `url(${imageURL})`,
    }"
  >
    <div class="icon">
      <img
        :src="require('@/assets/images/icons/' + icon.path)"
        v-for="icon in getIcon"
        v-bind:key="icon._id"
        :alt="icon.name"
      />
    </div>
    <div class="overlay" v-if="loaded">
      <div class="play-button" v-if="type == 0 || type == 2">
        <img :src="require('@/assets/images/icons/play.svg')" alt="Играть" />
      </div>
      <div class="stats">
        <div class="title" v-if="title == 0">{{ title }}</div>
        <div class="row">
          <div class="likes">
            <img
              :src="require('@/assets/images/icons/heart.svg')"
              alt="Лайки"
            />{{ likes }}
          </div>
          <div class="playbacks" v-if="type == 0">
            <img
              :src="require('@/assets/images/icons/play_mini.svg')"
              alt="Прослушивания"
            />10 850
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    type: {
      type: Number,
      default: 0,
    },
    imageURL: {
      type: String,
      default: "",
    },
    title: {
      type: String,
      default: "Название",
    },
    likes: {
      type: Number,
      default: 0,
    },
    loaded: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    getIcon() {
      switch (this.type) {
        case 0:
          return [
            {
              path: "soundcloud.svg",
              name: "SoundCloud",
            },
          ];
        case 1:
          return [
            {
              path: "instagram.svg",
              name: "Instagram",
            },
          ];
        case 2:
          return [
            {
              path: "video.svg",
              name: "Видео",
            },
            {
              path: "instagram.svg",
              name: "Instagram",
            },
          ];
        case 3:
          return [
            {
              path: "youtube.svg",
              name: "Youtube",
            },
          ];
        default:
          return [
            {
              path: "clocks.svg",
              name: "Загрузка",
            },
          ];
      }
    },
  },
};
</script>

<style lang="scss" scoped>
:root {
  --height: 200px;
}

.post {
  width: 100%;
  padding-bottom: 100%;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 32px;
  position: relative;
  overflow: hidden;
  background-size: cover;
  background-position: center;

  &.loading::after {
    content: "";
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    height: 80px;
    background-image: linear-gradient(
      transparent,
      rgba(255, 255, 255, 0.05),
      transparent 100%
    ) !important;

    animation: loading 3s infinite linear;
  }

  @keyframes loading {
    0% {
      top: -80px;
    }

    100% {
      top: 140%;
    }
  }

  .icon {
    float: right;
    padding: 16px;
    display: flex;
    align-items: center;
    position: absolute;
    right: 0;
    top: 0;

    img {
      margin-left: 12px;
      z-index: 10;
      width: 26px;
    }
  }

  &:hover {
    .overlay {
      opacity: 1;
      pointer-events: unset;
    }
  }

  .overlay {
    pointer-events: none;
    opacity: 0;
    z-index: 9;
    transition: 0.2s ease;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.9);

    .play-button {
      img {
        width: 100px;
        cursor: pointer;
        transition: 0.1s ease;

        &:hover {
          transform: scale(1.08);
        }
      }
    }

    .stats {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 24px;
      padding: 0 24px;

      .title {
        font-size: 1.2rem;
        margin-bottom: 16px;
      }

      .row {
        display: flex;
        justify-content: center;

        img {
          width: 20px;
          margin-bottom: 6px;
        }

        .likes,
        .playbacks {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          margin: 0 8px;
        }
      }
    }
  }
}

@media screen and (max-width: 1600px) {
  .post {
    border-radius: 16px;

    .icon {
      img {
        width: 26px;
      }
    }

    .overlay {
      .play-button {
        img {
          width: 80px;
        }
      }

      .stats {
        bottom: 16px;

        .title {
          padding: 0 8px;
          font-size: 1rem;
          margin-bottom: 10px;
        }
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .post {
    border-radius: 16px;

    .overlay {
      .play-button {
        img {
          width: 70px;
        }
      }

      .stats {
        bottom: 10px;

        .row {
          img {
            width: 14px;
          }

          .likes,
          .playbacks {
            font-size: 0.8rem;
          }
        }

        .title {
          font-size: 0.8rem;
        }
      }
    }
  }
}
</style>